import './App.css';
import data from './data';
import React, { useState, useEffect } from 'react';

function App() {
	const [people, setPeople] = useState(data);
	const [index, setIndex] = useState(0);

	useEffect(() => {
		const lastIndex = people.length - 1;
		if (index < 0) {
			setIndex(lastIndex);
		}
		if (index > lastIndex) {
			setIndex(0);
		}
	}, [index, people]);

	useEffect(() => {
		let slider = setInterval(() => {
			setIndex(index + 1);
		}, 3000);
		return () => clearInterval(slider);
	}, [index]);

	return (
		<div>
			<div className="App">
				{people.map((item, itemindex) => {
					const { id, name, image, title, quote } = item;
					let position = 'nextSlide';
					if (itemindex === index) {
						position = 'activeSlide';
					}
					if (
						itemindex === index - 1 ||
						(index === 0 && itemindex === people.length - 1)
					) {
						position = 'lastSlide';
					}
					return (
						<div className={`person ${position}`}>
							<h1>{name}</h1>
							<img src={image} alt="name" />
							<h4>{title}</h4>
							<h4>{quote}</h4>
						</div>
					);
				})}
			</div>
			<div className="btn">
				<button onClick={() => setIndex(index - 1)}>Prev</button>
				<button onClick={() => setIndex(index + 1)}>Next</button>
			</div>
		</div>
	);
}

export default App;
